<template>
  <div>
    <el-button type="primary" size="small" @click="adduser"></el-button>
    <el-button
      type="danger"
      size="small"
      @click="deleteUser"
      :disabled="delList.length == 0"
      ></el-button
    >
    <el-button
      type="primary"
      size="small"
      icon="el-icon-folder-add"
      @click="uploadFile"
      ></el-button
    >
    <el-button type="warning" size="small" @click="downModal"
      ></el-button
    >
    <input type="file" id="file" @change="chooseFile" style="display: none" />
    <el-form :inline="true" :model="form" style="margin-top: 15px">
      <el-form-item label="">
        <el-input
          v-model="form.name"
          clearable
          placeholder=""
          size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="form.account_id"
          clearable
          placeholder=""
          size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="form.phone"
          clearable
          placeholder=""
          size="small"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="branch_id" v-if="role == '1'">
        <el-select v-model="form.branch_id" clearable size="small">
          <el-option
            v-for="item in bankList"
            :key="item.branch_no"
            :label="item.branch_name"
            :value="item.branch_no"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          @click="getUserList"
          size="small"
          icon="el-icon-search"
          ></el-button
        >
      </el-form-item>
    </el-form>
    <base-table :list="dataList" :data="userList" @select="chooseUser">
      <el-table-column type="selection" slot="begin" width="40">
      </el-table-column>
      <el-table-column label="" align="center" width="240">
        <template slot-scope="scope">
          <template v-if="scope.row.role_id == role_id">
            <el-button
              type="text"
              icon="el-icon-edit-outline"
              @click="editUser(scope.row.account_id, true)"
              ></el-button
            >
          </template>
          <template v-else>
            <el-button
              type="text"
              icon="el-icon-edit-outline"
              @click="editUser(scope.row.account_id)"
              ></el-button
            >
            <el-button
              type="text"
              icon="el-icon-delete"
              style="color: red"
              @click="delOne(scope.row.account_id)"
              ></el-button
            >
            <el-dropdown placement="bottom">
              <span
                class="el-dropdown-link"
                style="color: #e6a23c; margin-left: 10px"
              >
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item @click.native="resetPassword(scope.row, '1')"
                  ></el-dropdown-item
                >
                <el-dropdown-item @click.native="resetPassword(scope.row, '2')"
                  ></el-dropdown-item
                >
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </template>
      </el-table-column>
    </base-table>
    <base-page
      :page="pageNum"
      :size="pageSize"
      :total="total"
      @size="changeSize"
      @page="changePage"
    ></base-page>
  </div>
</template>

<script>
import baseTable from "../../components/Table";
import basePage from "../../components/Pagination";
import XLSX from "xlsx";

export default {
  name: "admin",
  components: {
    baseTable,
    basePage,
  },
  data() {
    return {
      user: JSON.parse(sessionStorage.getItem("user")),
      role_id: sessionStorage.getItem("role_id"),
      role: sessionStorage.getItem("role"),
      form: {},
      dataList: [
        {
          label: "",
          prop: "account_id",
          sort: true,
        },
        {
          label: "",
          prop: "name",
        },
        {
          label: "",
          prop: "phone",
        },
        {
          label: "",
          prop: "branch_name",
        },
        {
          label: "",
          prop: "role_name",
        },
        {
          label: "",
          prop: "create_time",
          width: 120,
        },
      ],
      userList: [],
      delList: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      flag: "",
      newUserList: [],
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      },
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
  },
  mounted() {
    this.getBankList();
    // this.getUserList();
  },
  methods: {
    // 
    getBankList() {
      if (this.bankList.length != 0) return;
      this.$api.getBranch().then((res) => {
        this.bankList = res.data;
      });
    },
    // 
    adduser() {
      this.$router.push({
        path: "./adduser",
        name: "",
        params: {
          role: this.role,
        },
      });
    },
    // 
    editUser(id, flag) {
      this.$router.push({
        path: "./adduser",
        name: "",
        query: {
          id: id,
        },
        params: {
          role: this.role,
          searchKey: this.form,
          check: flag, // 
        },
      });
    },
    // 
    getUserList() {
      this.$api
        .getUserList({
          page: this.pageNum - 1,
          number: this.pageSize,
          ...this.form,
        })
        .then((res) => {
          this.userList = res.data.userList;
          this.total = res.data.total;
          // this.form = {};
        });
    },
    // 
    changeSize(size) {
      this.pageNum = 1;
      this.pageSize = size;
      this.getUserList();
    },
    // 
    changePage(page) {
      this.pageNum = page;
      this.getUserList();
    },
    delOne(id) {
      this.delList = [];
      this.delList.push(id);
      this.deleteUser();
    },
    // 
    chooseUser(data) {
      this.delList = data.map((item) => {
        return item.account_id;
      });
    },
    // 
    deleteUser() {
      console.log(this.delList);
      if (this.delList.indexOf("admin") > -1) {
        this.$message.error("");
        return;
      }
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
        center: true,
      })
        .then(() => {
          this.$api
            .deleteUser({
              accountIds: this.delList,
            })
            .then((res) => {
              console.log(res);
              this.getUserList();
              this.$message.success("");
              this.delList = [];
            });
        })
        .catch(() => {
          this.delList = [];
        });
    },
    // /
    resetPassword(row, flag) {
      this.$confirm(
        `${
          flag == "1" ? "" : ""
        }password`,
        "",
        {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
          center: true,
        }
      ).then(() => {
        this.$api
          .resetPassword({
            account_id: row.account_id,
            flag: flag,
          })
          .then((res) => {
            console.log(res);
            this.$notify({
              title: "",
              duration: 3000,
              message: `${row.name}${
                flag == "1" ? "" : ""
              }password`,
              type: "success",
            });
          });
      });
    },
    // Excel
    uploadFile() {
      let input = document.getElementById("file");
      input.value = "";
      input.click();
    },
    chooseFile() {
      let that = this;
      let input = document.getElementById("file");
      let file = input.files[0];
      input.value = "";
      this.$api
        .uploadUser({
          file: file,
        })
        .then((res) => {
          // console.log(res);
          if (res.fail_count == 0) {
            this.$notify.success(`${res.success_count}`);
          } else {
            let arr = "";
            res.exist_account_id_list.map((item) => {
              arr = arr + item.account_id + ", ";
            });
            arr = arr.substring(0, arr.length - 2);
            this.$notify.info({
              message: `${res.total_count}${res.success_count}
              ${res.fail_count}${arr}`,
            });
          }
          this.getUserList();
        });
    },
    // 
    downModal() {
      var aTag = document.createElement("a");
      aTag.download = ".xlsx";
      aTag.href = this.baseURL + "download/account_import.xlsx";
      aTag.click();
    },
  },
  // 
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      if (to.params.searchKey) {
        vm.form = to.params.searchKey;
      }
      vm.getUserList();
    });
  },
};
</script>



