<template>
  <div>
    <el-form
      ref="form"
      status-icon
      :rules="rules"
      :model="form"
      label-width="120px"
    >
      <el-form-item label="" prop="account_id" style="width: 360px">
        <el-input
          v-model="form.account_id"
          :disabled="account_id ? true : false"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="name" style="width: 360px">
        <el-input v-model="form.name" :disabled="check"></el-input>
      </el-form-item>
      <el-form-item label="" prop="identify_no" style="width: 360px">
        <el-input v-model="form.identify_no" :disabled="check"></el-input>
      </el-form-item>
      <el-form-item
        label=""
        prop="branch_id"
        v-if="form.account_id != 'admin'"
      >
        <el-select
          v-model="form.branch_id"
          :disabled="$route.params.role != 1 || check"
        >
          <el-option
            v-for="item in bankList"
            :key="item.branch_no"
            :label="item.branch_name"
            :value="item.branch_no"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="role">
        <el-radio-group v-model="form.role" :disabled="check">
          <el-radio
            v-for="item in userList"
            :key="item.role_id"
            :label="item.role_id"
            >{{ item.role_name }}</el-radio
          >
        </el-radio-group>
        <!-- <el-input v-else v-model="form.name" :disabled="check"></el-input> -->
      </el-form-item>
      <el-form-item label="" prop="phone" style="width: 360px">
        <el-input v-model="form.phone" :disabled="check"></el-input>
      </el-form-item>
      <el-form-item
        label=""
        prop="login_pwd"
        v-if="!account_id"
        style="width: 360px"
      >
        <el-input
          v-model="form.login_pwd"
          show-password
          :disabled="check"
        ></el-input>
      </el-form-item>
      <!-- <el-form-item
        label=""
        prop="business_pwd"
        v-if="!account_id"
        style="width: 360px"
      >
        <el-input v-model="form.business_pwd" show-password></el-input>
      </el-form-item> -->
      <el-form-item>
        <el-button
          v-if="!check"
          type="primary"
          size="medium"
          @click="onSubmit"
          >{{ account_id ? "" : "" }}</el-button
        >
        <el-button v-else type="primary" size="medium" @click="onSubmit"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "adduser",
  data() {
    var checkID = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      // 
      var idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
      if (idcardReg.test(value)) {
        callback();
      } else {
        return callback(new Error(""));
      }
    };
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      var phoneReg = /^1[3456789]\d{9}$/; // 
      if (phoneReg.test(value)) {
        callback();
      } else {
        return callback(new Error(""));
      }
    };
    var checkNum = (rule, value, callback) => {
      if (!value) {
        return callback(new Error(""));
      }
      var numberReg = /^\d+$/g;
      if (numberReg.test(value)) {
        callback();
      } else {
        return callback(new Error(""));
      }
    };
    return {
      form: {
        roleids: [],
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 5, message: " 2  5 ", trigger: "blur" },
        ],
        account_id: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkNum, trigger: "blur" },
        ],
        identify_no: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkID, trigger: "blur" },
        ],
        branch_id: [
          { required: true, message: "", trigger: "change" },
        ],
        role: [{ required: true, message: "", trigger: "change" }],
        phone: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPhone, trigger: "blur" },
        ],
        login_pwd: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 20,
            message: " 6  20 ",
            trigger: "blur",
          },
        ],
        // business_pwd: [
        //   { required: true, message: "", trigger: "blur" },
        //   {
        //     min: 6,
        //     max: 20,
        //     message: " 6  20 ",
        //     trigger: "blur",
        //   },
        // ],
      },
      account_id: "",
      userList: [],
      check: false,
    };
  },
  computed: {
    bankList: {
      get: function () {
        return this.$store.state.bankList;
      },
      set: function (newVal) {
        this.$store.state.bankList = newVal;
      },
    },
  },
  mounted() {
    this.check = this.$route.params.check;
    let role = this.$route.params.role;
    if (role == 2) {
      let info = JSON.parse(sessionStorage.getItem("user"));
      this.$set(this.form, "branch_id", info.branch_id);
    }
    this.account_id = this.$route.query.id;
    if (this.account_id) {
      this.$api
        .getUserInfo({
          account_id: this.account_id,
        })
        .then((res) => {
          console.log(res.data);
          this.form = res.data.user;
          if (this.check) {
            this.userList = res.data.roleList;
          } else {
            this.getUsertype();
          }
          // if (
          //   this.$route.params.role == "1" &&
          //   res.data.user.account_id != "admin"
          // ) {
          //   this.getUsertype();
          // } else {
          //   this.userList = res.data.roleList;
          // }
          this.$set(this.form, "role", res.data.roleList[0].role_id);
        });
    } else {
      this.getUsertype();
    }
    this.getBankList();
  },
  methods: {
    // 
    getBankList() {
      if (this.bankList.length != 0) return;
      this.$api.getBranch().then((res) => {
        this.bankList = res.data;
      });
    },
    // 
    getUsertype() {
      if (this.check) return;
      this.$api.getUsertype().then((res) => {
        this.userList = res.data;
        if (res.data.length == 1) {
          this.$set(this.form, "role", res.data[0].role_id);
        }
      });
    },
    onSubmit() {
      if (this.check) {
        this.$router.back(-1);
        return;
      }
      this.$set(this.form, "roleids", [this.form.role]);
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (this.account_id) {
            delete this.form.create_time;
            this.$api.updateUser(this.form).then((res) => {
              console.log(res.data);
              this.$message({
                message: "",
                type: "success",
              });
              this.$router.back(-1);
            });
          } else {
            this.$api.addUser(this.form).then((res) => {
              console.log(res.data);
              this.$message({
                message: "",
                type: "success",
              });
              this.$router.back(-1);
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  // 
  beforeRouteLeave(to, from, next) {
    to.params.searchKey = this.$route.params.searchKey;
    next();
  },
};
</script>



