<template>
  <div>
    <!-- Excel -->
    <div style="display: flex; justify-content: space-around">
      <el-upload
        action="/"
        :on-change="onChange"
        :auto-upload="false"
        :show-file-list="false"
        accept=".xls, .xlsx"
      >
        <el-button type="warning" icon="el-icon-folder-add"></el-button>
        <!-- <div slot="tip" v-if="fileData">{{ fileData.name }}</div> -->
      </el-upload>
      <el-button type="warning" icon="el-icon-download" @click="exportToExcel"
        ></el-button
      >
    </div>

    <el-table fit :data="tableData" style="width: 100%">
      <el-table-column prop="id" label="id"> </el-table-column>
      <el-table-column prop="stuName" label=""> </el-table-column>
      <el-table-column prop="stuNumber" label=""> </el-table-column>
      <el-table-column prop="sex" label=""> </el-table-column>
      <el-table-column prop="xueyuan" label=""> </el-table-column>
      <el-table-column prop="banji" label=""> </el-table-column>
      <el-table-column prop="sushe" label=""> </el-table-column>
    </el-table>
  </div>
</template>

<script>
import XLSX from "xlsx";

export default {
  data() {
    return {
      tableData: [],
    };
  },
  methods: {
    // 
    onChange(file, fileList) {
      console.log(fileList);
      this.fileData = file; // 
      this.readExcel(); // 
    },
    // 
    readExcel(e) {
      console.log(e);
      let that = this;
      const files = that.fileData;
      console.log(files);
      if (!files) {
        //
        return false;
      } else if (!/\.(xls|xlsx)$/.test(files.name.toLowerCase())) {
        this.$message.error("xlsxlsx");
        return false;
      }
      const fileReader = new FileReader();
      fileReader.onload = (ev) => {
        try {
          const data = ev.target.result;
          // console.log(data)
          const workbook = XLSX.read(data, {
            type: "binary",
          });
          console.log(workbook);
          console.log(workbook.SheetNames);
          if (workbook.SheetNames.length >= 1) {
            this.$message({
              message: "",
              showClose: true,
              type: "success",
            });
          }
          const wsname = workbook.SheetNames[0]; //
          const ws = XLSX.utils.sheet_to_json(workbook.Sheets[wsname]); //json
          console.log(ws);
          that.outputs = []; //
          for (var i = 0; i < ws.length; i++) {
            var sheetData = {
              //  el  ws[i][]
              id: ws[i]["id"],
              stuName: ws[i][""],
              stuNumber: ws[i][""],
              sex: ws[i][""],
              xueyuan: ws[i][""],
              banji: ws[i][""],
              sushe: ws[i][""],
            };
            that.tableData.unshift(sheetData);
          }
          this.$refs.upload.value = "";
        } catch (e) {
          console.log(e);
          return false;
        }
      };
      //  input  files[0]
      fileReader.readAsBinaryString(files.raw);
    },
    exportToExcel() {
      //excel
      require.ensure([], () => {
        const { export_json_to_excel } = require("../../../excel/Export2Excel");
        const tHeader = [
          "id",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
        ];
        const filterVal = [
          "id",
          "stuNumber",
          "stuName",
          "sex",
          "tel",
          "sushe",
          "xueyuan",
          "banji",
          "lyNumber",
        ];
        const list = this.tableData;
        const data = this.formatJson(filterVal, list);
        export_json_to_excel(tHeader, data, "excel");
      });
    },
    formatJson(filterVal, jsonData) {
      return jsonData.map((v) => filterVal.map((j) => v[j]));
    },
  },
};
</script>

<style scoped></style>



