<template>
  <div>
    <!--  -->
    <!--   -->
    <el-form
      ref="form"
      :model="user"
      label-width="80px"
      style="width: 480px"
      v-if="role == '4'"
    >
      <el-form-item label="">
        <el-input v-model="user.shop_name" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="user.shop_id" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="user.shop_type" disabled>
          <el-option
            v-for="item in typeList"
            :key="item.type_id"
            :label="item.type_name"
            :value="item.type_id"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="user.link_person" :disabled="!infoBtn"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="user.link_phone" :disabled="!infoBtn"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="user.branch_name"
          disabled
          style="width: 400px"
        ></el-input>
      </el-form-item>
      <el-form-item inline v-if="!infoBtn">
        <el-button
          type="primary"
          @click="
            () => {
              this.infoBtn = true;
            }
          "
          size="small"
          ></el-button
        >
      </el-form-item>
      <el-form-item inline v-if="infoBtn">
        <el-button type="primary" @click="handleChange" size="small"
          ></el-button
        >
        <el-button
          type="primary"
          @click="
            () => {
              this.infoBtn = false;
            }
          "
          size="small"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <!-- :  -->
    <!--  -->
    <el-form
      ref="form"
      :model="user"
      label-width="80px"
      style="width: 480px"
      v-else
    >
      <el-form-item label="">
        <el-input v-model="user.name" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="user.account_id" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="user.branch_name"
          disabled
          style="width: 300p x"
        ></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="user.phone" :disabled="!infoBtn"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          v-model="user.role_name"
          disabled
          style="width: 400px"
        ></el-input>
      </el-form-item>
      <el-form-item inline v-if="!infoBtn">
        <el-button type="primary" @click="infoBtn = true" size="small"
          ></el-button
        >
        <el-button type="primary" @click="changePwd('loginPwd')" size="small"
          ></el-button
        >
        <el-button type="primary" @click="changePwd('businessPwd')" size="small"
          ></el-button
        >
      </el-form-item>

      <el-form-item inline v-if="infoBtn">
        <el-button type="primary" @click="handleChange()" size="small"
          ></el-button
        >
        <el-button
          type="primary"
          @click="
            () => {
              this.infoBtn = false;
            }
          "
          size="small"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <!--  -->
    <el-dialog
      :title="type == 'loginPwd' ? '' : ''"
      :visible.sync="pwdDialog"
      width="30%"
      :before-close="handleClose"
    >
      <el-form
        ref="ruleForm"
        status-icon
        :model="ruleForm"
        :rules="rules"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="old_pwd">
          <el-input v-model="ruleForm.old_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="new_pwd">
          <el-input v-model="ruleForm.new_pwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirm_pwd">
          <el-input v-model="ruleForm.confirm_pwd" type="password"></el-input>
        </el-form-item>
        <div class="dialog-footer" style="text-align: center">
          <el-button
            @click="
              () => {
                this.pwdDialog = false;
              }
            "
            size="small"
            > </el-button
          >
          <el-button
            type="primary"
            @click="changePwdSure('ruleForm')"
            size="small"
            > </el-button
          >
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      role: "1",
      user: {},
      rules: {
        old_pwd: [
          { required: true, message: "", trigger: "blur" },
          // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        new_pwd: [
          { required: true, message: "", trigger: "blur" },
          // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        confirm_pwd: [
          { required: true, message: "", trigger: "blur" },
          // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
      },
      ruleForm: {},
      infoBtn: false,
      pwdDialog: false,
      flag: "0",
      type: "",
    };
  },
  mounted() {
    this.role = sessionStorage.getItem("role");
    this.getMyInfo();
    this.getTypeList();
  },
  computed: {
    typeList: {
      get: function () {
        return this.$store.state.typeList;
      },
      set: function (newVal) {
        this.$store.state.typeList = newVal;
      },
    },
  },
  methods: {
    // 
    getMyInfo() {
      this.$api
        .getMyInfo({
          flag: this.role == "4" ? "1" : "0",
        })
        .then((res) => {
          this.user = res.data.result;
          this.flag = res.data.type;
        });
    },
    // 
    getTypeList() {
      if (this.typeList.length != 0) return;
      this.$api.getBusinessType().then((res) => {
        this.typeList = res.data;
      });
    },
    // 
    handleChange() {
      if (!this.user.phone && !this.user.link_phone) {
        this.$message({
          message: "",
          type: "error",
        });
        return;
      }
      let express = /^1[34578]\d{9}$/;
      if (!/^1[34578]\d{9}$/.test(this.user.phone || this.user.link_phone)) {
        this.$message({
          message: "",
          type: "error",
        });
        return;
      }
      // /
      this.$api
        .updateUserInfo({
          flag: this.flag,
          phone: this.user.phone,
          link_phone: this.user.link_phone,
          link_person: this.user.link_person,
        })
        .then((res) => {
          console.log(res.data);
          this.infoBtn = false;
          this.$message.success("");
          this.getMyInfo();
        });
    },
    //   
    changePwd(type) {
      this.pwdDialog = true;
      this.type = type;
      console.log("type: ", type);
    },
    // 
    changePwdSure(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.ruleForm.new_pwd === this.ruleForm.confirm_pwd) {
            if (this.type == "loginPwd") {
              this.$api.updateLoginPwd(this.ruleForm).then((res) => {
                console.log(res.data);
                this.pwdDialog = false;
                this.ruleForm = {};
                this.$message.success("");
              });
            } else {
              this.$api.updateBusinessPwd(this.ruleForm).then((res) => {
                console.log(res.data);
                this.pwdDialog = false;
                this.ruleForm = {};
                this.$message.success("");
              });
            }
          } else {
            this.$message({
              message: "",
              type: "error",
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    handleClose() {
      this.pwdDialog = false;
      this.ruleForm = {};
    },
  },
};
</script>

<style></style>



